.project-item {
    margin: 0 1em 2em 1em ;

    width: 20vw;
    height: 22vw;

    display: flex;
    flex-direction: column;
    box-shadow: 0px 4px 14px -6px rgba(0,0,0,0.75);
    border-radius: 15px;
    overflow: hidden;
    position: relative;


    &:hover {

        & .overlay .image {
            opacity: 0.1;
            transform: scale(1.1);
            transition: all 500ms cubic-bezier(0.25, 0.45, 0.45, 0.95);

        }

        & .icons {
            color:$main_light;
            transition: all 500ms cubic-bezier(0.25, 0.45, 0.45, 0.95);
        }


    }

    .overlay {
        height: 50%;
        width: 100%;
        background: rgb(11, 19, 43);
        overflow: hidden;

        .image {

            height: 100%;
            width: 100%;
            background-size: cover;
            background-position: center;
            background-repeat: no-repeat;
            border-radius: 15px 15px 0 0 ;
            opacity: 0.6;


        }
    }

    .icons {
        position: absolute;
        height: 50%;
        width: 100%;
        font-size: 2rem;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 10;
        color: white;
        .icon {
            font-size: 2.8rem;
            margin: 0.3rem;


            &:hover {
                transform: scale(1.3);
                transition: all 250ms cubic-bezier(0.25, 0.45, 0.45, 0.95);
            }
        }
    }



    .description {
        height: 40%;
        width: 80%;
        margin: auto;
        text-align: center;
        overflow: hidden;

        h3 {
            margin: 0.8rem 0 0.5rem 0 ;
            font-family: 'Montserrat', sans-serif
        }

        p {
            font-size: 1em;
            margin:0;
        }

    }
    .tech-stack {
        height: 16%;
        width: 100%;
        font-size:1.5rem;
        display: flex;
        justify-content: center;
        color: $dark_light;
        position: relative;


        div {

            padding: 0.4rem 5px 0 5px;
            margin: auto 0;
            border-top: 1px solid $main_light;


        }
    }

/*     @media (max-width: 600px) {
        width: 60vw;
        height: 66vw;
    } */

}
