body {
  margin: 0;
  padding:0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}



/*COLOR PALETTE*/


$main_dark: rgb(11, 19, 43);
$main_light: rgb(25, 210, 148);
$sec_dark: #1c2541;
$sec_light: #7cffc4;
$dark_light: #3a506b;
$light: rgb(234, 235, 237);

/*Size vars*/
$small: 768px;
$medium: 1024px;
$laptop:1440px;
$fourk: 2560px;
$smallFont:12px;

/*IMPORTS*/

/*components styles*/
@import './App.scss';
@import './components/app-bar/app-bar.styles.scss';
@import './components/hamburger-icon/hamburger.styles.scss';
@import './components/landing-text/landing-text.styles.scss';
@import './components/contact-form/contact-form.styles.scss';
@import './components/items-showcase/items-showcase.styles.scss';
@import './components/items-showcase/item/item.styles.scss';

/*sections styles*/
@import './sections/landing/landing.scss';
@import './sections/projects/projects.styles.scss';
@import './sections/about/about.styles.scss';
@import './sections/footer/footer.styles.scss';

/*Fonts */

@font-face {
  font-family: 'Odachi';
  src: url('./Webfont/Odachi.woff') format('woff'); /* Pretty Modern Browsers */
}

/*global styles */



h2 {
  color:$sec_dark;
  font-size: 2em;
  text-align: center;
  font-family: 'Odachi';
  margin:1.5em 0 1.2em 0;
 
  span {
      padding-bottom: 0.5rem;
      border-bottom: solid 2px $main_light;
  }
}

h3 {
  text-transform: uppercase;
  color:$main_dark;
  font-size: 1.3em;
}

h4 {
  color:$sec_dark;
  font-size: 1.1em;
  font-weight: 400;
}

button {
  color:$main_light;
  margin: 1em 0;
  padding:0.8em;
  border: 2px solid  $main_light;
  background: rgb(11,19,43);

  border-radius: 15px;
  cursor: pointer;
  font-size: 1em;
  text-transform: uppercase;
  &:hover {
      color:$sec_light;
      background: linear-gradient(180deg, rgba(11,19,43,1) 0%, rgba(28,37,65,1) 100%);
     
  }
}

.container {
  width:90%;
  margin: auto;
}





/* Resize support */

@media screen and (max-width:$fourk) {
  body {
    font-size: 22px;
  }
}

@media screen and (max-width:1920px) {
  body {
    font-size: 17px;
  }

  .project-item  {
    
    h3 {
      margin-bottom: 0;
    }

    p {
      
      font-size: 15px;
    }
  }
}



@media screen and (max-width:$laptop) {
  body {
    font-size: 16px;
  }

   .project-item  {
    width: 27vw;
    height: 28vw; 
    font-size: 14px;
  }
}

@media screen and (max-width:$medium) {
  body {
    font-size: 15px;
  }

  .project-item  {
    width: 40vw;
    height: 40vw; 
  }


}

@media screen and (max-width:$small) {
body {
  font-size: 13px;
}

  .nav-box {
    z-index: 1000;
      display: flex;
      flex-direction: column;
      position: fixed;
      text-align: center;
      top:0;
      left:0;
      height: 100%;
      width:100%;
    
      justify-content: flex-start !important;
      background: $main_dark;
      flex-direction: column;
      clip-path: circle(100px at 90% -20%);
      -webkit-clip-path: circle(100px at 90% -20%);
      transition: all 1s ease-out;
      pointer-events: none;
       .nav-item{
         padding: 2em !important;
         font-size:2em

       }


      &.open {
        pointer-events: all;
        clip-path: circle(1000px at 90% -20% );
        -webkit-clip-path: circle(1000px at 90% -20%);
      }
    }

  //PROJECTS

  .projects .gallery .wrapper {
    width: 40vw;
    height: 40vw; 
  }


  //ABOUT
    .about .about-wrapper{

      .portret {
        top:-5%;
      }
    
    
    .text-section {
      flex-direction: column;
      
 

      .skills,.about-me,.hobbies {
       
        width:80%;
      }
    }
  }

  .footer .footer-contain{
    flex-direction: column;

    .mail,.phone,.madeBy {
      width:80%;
    }
  }

  .form {
  
    form {
      width: 100%;
    }
  
    .right {
      display:none;
    }
  }

}

@media screen and (max-width:664px) {
  body {
    font-size: 12px;
  }

  .project-item  {
    width: 60vw;
    height: 60vw; 
  }
}

@media screen and (max-width:320px) {
  body {
    font-size: 12px;
  }

  .project-item  {
    width: 80vw;
    height: 80vw; 
  }
}

